<template>
  <div class="container">
    <div class="bg">
      <img src="../images/yarhushta.png" alt="">
      <div class="overlay">
        <h1>Armenian Youth</h1>d
        <a :href="telegramLink">
          Hello
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'BusinessCard',
  data () {
    return {
      telegramLink: 'https://t.me/joinchat/E53qM0ofa2Sw0EaH7k70kA'
    }
  }
}
</script>

<style lang="scss" scoped>
  $bg: rgba(0, 0, 0, 0.2);

  *,
  *:before,
  *:after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  .container {
    width: 85vw;
    margin: 1rem auto;
  }
  .bg,
  .overlay {
    text-align: center;
  }

  img,
  .overlay {
    transition: .3s all;
    border-radius: 3px;
  }

  .bg {
    float: left;
    max-width: 31%;
    position: relative;
    margin: .5%;
    img {
      width: 100%;
      margin-bottom: -4px;
    }
    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      width: 100%;
      background: $bg;
      color: #fff;
      opacity: 0;
      h2 {
        padding-top: 20%;
        font-family: 'Droid Serif', serif;
      }
      p {
        font-family: 'Julius Sans One', sans-serif;
      }
    }
    &:hover {
      .overlay {
        opacity: 1;
      }
      img {
        -webkit-filter: blur(2px);
        filter: blur(2px);
      }
    }
  }

  @media screen and (max-width: 1148px) {
    .bg {
      max-width: 48%;
      margin: 1%;
    }
  }

  @media screen and (max-width: 768px) {
    .bg {
      float: none;
      max-width: 80%;
      margin: 1% auto;
    }
  }
</style>



